@import url(https://fonts.googleapis.com/css2?family=Fredoka+One&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.movie-poster-image{height:auto;width:200px;border:1px solid black}.movie-poster-image:hover{animation:shadow-pop-tr 0.3s cubic-bezier(0.47, 0, 0.745, 0.715) both;cursor:pointer}button{border:none}.thumbnail-display-info{text-align:center;overflow-wrap:anywhere}.thumbnail{align-self:center;width:150px;margin:20px;padding:20px}@keyframes shadow-pop-tr{0%{box-shadow:0 0 #3e3e3e,0 0 #3e3e3e,0 0 #3e3e3e,0 0 #3e3e3e,0 0 #3e3e3e,0 0 #3e3e3e,0 0 #3e3e3e,0 0 #3e3e3e;transform:translateX(0) translateY(0)}100%{box-shadow:1px -1px #3e3e3e,2px -2px #3e3e3e,3px -3px #3e3e3e,4px -4px #3e3e3e,5px -5px #3e3e3e,6px -6px #3e3e3e,7px -7px #3e3e3e,8px -8px #3e3e3e;transform:translateX(-8px) translateY(8px)}}

.thumbnail-container{display:flex;flex-direction:row;justify-content:space-around;flex-wrap:wrap;margin-right:40px}@media (max-width: 800px){.thumbnail-container{flex-direction:column !important}}

.header{display:flex;justify-content:center;align-items:center;text-align:center;height:35vh;margin-bottom:20px;background-size:cover !important;width:100vw;background-repeat:no-repeat !important}@media (min-width: 800px){.header{height:60vh}}@media (min-width: 1200px){.header{height:70vh}}.header-button[disabled]{pointer-events:none}.header-text{-webkit-text-stroke:1px black;color:white;text-shadow:3px 3px 0 #000, -1px -1px 0 #000,   1px -1px 0 #000, -1px 1px 0 #000, 1px 1px 0 #000;font-family:'Fredoka One', cursive;line-height:1.3em;letter-spacing:5px;font-size:4em}@media (min-width: 800px){.header-text{font-size:5em}}@media (min-width: 1200px){.header-text{font-size:7em}}.header-text:hover{color:green;cursor:pointer}

.movie-section{background-color:#F4F2F2}.movie-info{margin:0px 20px 0px 20px;display:flex;flex-direction:row;justify-content:space-around;align-items:center;height:500px}@media (max-width: 800px){.movie-info{flex-direction:column !important}}.movie-poster{box-shadow:5px 5px 5px black;height:400px;margin:0px 30px 0px 30px}.movie-text{height:auto;display:flex;flex-direction:column;justify-content:center;text-align:center;margin:0px 50px 0px 50px}.more-info{display:flex;flex-direction:column;justify-content:space-around;flex-wrap:wrap;margin:10px}.details{display:flex;justify-content:space-around;flex-direction:row;padding:10px}@media (max-width: 800px){.details{flex-direction:column !important}}.back-button{font-size:15px;color:white;text-align:center;background-color:rgba(0,0,0,0.81);padding:10px;align-self:center;border:2px solid black;cursor:pointer}.back-button:hover{background-color:green}

.videos-section{width:60vw;height:auto}.youtube-video{object-fit:cover}

.search-container{display:flex;justify-content:center;margin:30px}.search-bar{text-align:center;width:50vw;height:30px;box-shadow:3px 3px 5px black}

.rating-filter{display:flex;flex-direction:column;justify-content:center;align-items:center;color:white;margin:-30px 0px 20px 0px}.rating-filter-form{flex-direction:row;justify-content:center}input{margin:10px}

main{margin:0;background-color:#F4F2F2}button{background:none;outline:none}.search-area{display:flex;flex-direction:column;justify-content:center;align-content:center;margin-top:-35px}

